import Evento from './Evento';

const Eventos = (props) => {
    return ( 
        <div className="row">
            {Object.keys(props.eventos).map(key => (
                <Evento
                    key={key}
                    info={this.props.eventos[key]}
                />
            ))}
            
        </div>
     );
}
 
export default Eventos;

